button.remove {
  width: 20px;
  height: 20px;

  padding: 0;

  background-color: rgba(0,0,0,.75);
  color: var(--paper-color);
  border: none;
  border-radius: 50%;

  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
button.remove:focus {
  outline: none;
  
  -webkit-box-shadow: var(--focus);
  
          box-shadow: var(--focus);
}
.Event button.remove {
  position: absolute;
  right: 0;
  bottom: 0;
}



