.Controls {
  width: 240px;
  min-height: 630px;

  margin-right: 20px;

  -ms-flex: none;

      flex: none;

  position: relative;
  z-index: 99;
}

h1 {
  font-size: 24px;

  margin: 0 0 10px;
}

h2 {
  font-size: 13px;
  text-transform: uppercase;

  margin: 10px 0 3px;
}

form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
}
form input[type=text],
form select {
  -ms-flex: 1 1;
      flex: 1 1;
}
form * {
  margin-bottom: 3px;
}

label {
  margin-right: 3px;
}

form.import-weeks {
  display: grid;
  grid-template-columns: 75px 165px;
}
form.import-weeks input[type=submit] {
  grid-column: 1 / 3;
}

form.unit-colours {
  display: grid;
  grid-template-columns: 50px 1fr;
}



